import React, { Component } from 'react';
import "./likeButton.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faThumbsUp, faCheck } from "@fortawesome/free-solid-svg-icons";

const thumbsUp = <FontAwesomeIcon icon={faThumbsUp} className="thumbsUp" />
const check = <FontAwesomeIcon icon={faCheck} className="check" />


class LikeButton extends Component {
    state = {  
        liked: localStorage.getItem('liked') || false,
        clicks: localStorage.getItem('clicks') || 23,
        nextLiked: localStorage.getItem('nextLiked') || true,
        nextClicks: localStorage.getItem('nextClicks') || 24
    }

    
    componentDidMount() {
        if (!localStorage.getItem('liked')) {
            const { clicks, liked } = this.state;
            localStorage.setItem('liked', liked)
            localStorage.setItem('clicks', clicks)
        }
        // const liked = localStorage.getItem('liked') ? localStorage.getItem('liked') : false;
        // const clicks = localStorage.getItem('clicks') ? localStorage.getItem('clicks') : 23;
        // this.setState({ liked, clicks});
        
    }
    

    // HandleClick = () => {
    //     const { clicks, liked} = this.state
    //     if (!this.state.liked) {
    //         const nextLiked = true;
    //         const nextClicks = this.state.clicks + 1;
    //     } else {
    //         const nextLiked = false;
    //         const nextClicks = this.state.clicks - 1;
    //     };
    //     this.setState({
    //         liked: nextLiked,
    //         clicks: nextClicks
    //     })

    //     localStorage.setItem('liked', liked);
    //     localStorage.setItem('clicks', clicks)
    // };

    HandleClick = () => {
        const { clicks, liked, nextLiked, nextClicks} = this.state
        if (!this.state.liked) {
            this.setState({
                liked: true,
                clicks: this.state.clicks + 1,
                nextLiked: false,
                nextClicks: this.state.nextClicks - 1
                
            });
        } else {
            this.setState({
                liked: false,
                clicks: this.state.clicks - 1,
                nextLiked: true,
                nextClicks: this.state.nextClicks + 1
            })
        };
        
        localStorage.setItem('liked', liked);
        localStorage.setItem('clicks', clicks);
        localStorage.setItem('nextLiked', nextLiked);
        localStorage.setItem('nextClicks', nextClicks)
    };
    
    // HandleClick = () => {
    //     const { clicks, liked} = this.state
    //     if (!this.state.liked) {
    //         this.setState({
    //             liked: true,
    //             clicks: this.state.clicks + 1,
    //             nextLiked: false,
    //             nextClicks: this.state.clicks - 1
                
    //         });
    //     } else {
    //         this.setState({
    //             liked: false,
    //             clicks: this.state.clicks - 1,
    //             nextLiked: true,
    //             nextClicks: this.state.clicks + 1
    //         })
    //     };
        

    //     localStorage.setItem('nextLiked', liked);
    //     localStorage.setItem('nextClicks', clicks)
    // };
    
    HandleStorage = () => {
        const { clicks, liked} = this.state
        localStorage.setItem('clicks', clicks);
        localStorage.setItem('liked', liked);
    }

    // HandleClick = () => {
    //     const { clicks, liked, nextLiked, nextClicks } = this.state
    //     if (!this.state.liked) {
    //         this.setState({
    //             nextLiked: true,
    //             nextClicks: clicks + 1
    //         });
    //     } else {
    //         this.setState({
    //             nextLiked: false,
    //             nextClicks: clicks - 1
    //         })
    //     };
    //     this.setState({
    //         'liked': nextLiked,
    //         'clicks': nextClicks
    //     });
    //     localStorage.setItem('clicks', clicks);
    //     localStorage.setItem('liked', liked);
    //     // localStorage.setItem('nextLiked', liked);
    //     // localStorage.setItem('nextClicked', clicks)
    // };


    render() { 
        return ( 
            <button className="like-button" onClick={this.HandleClick}>
            {this.state.liked ? check : thumbsUp}
            Like |
            <span className="likes-counter">{this.state.clicks}</span>
          </button>
          
         );
    }
}
 
export default LikeButton;